
.statemen_cap {
    padding-bottom: 50px;
    border-bottom: 1px solid rgba(#3B4256, .3);
}
.statemen_cap_wrapper {
    position: relative;
    top: 0;
    left: 125px;
}

.statemen_logo {
    width: 80px;
    height: 80px;
    margin: 16px 0;
}

.statemen_name {
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    color: #3B4256;
}

.statemen_cap_title {
    font-size: 63px;
    font-weight: 700;
    line-height: 56px;
    color: #3B4256;
}

.statemen_cap_des {
    width: 90%;
    font-size: 22px;
    line-height: 24px;
    color: #3B4256;
}

.statemen_inner {
    width: 900px;
    margin-top: 40px;
}

@import './StyleBlocks/DataForm.scss';
@import './StyleBlocks/Applicatns.scss';
@import './StyleBlocks/PersonalData.scss';
@import './StyleBlocks/Consent.scss';


